import React from 'react';
import { Checkbox, Button, Icon, TextInput, Row, Col } from 'react-materialize';

export default function Tasks ({tasks, onTasksChange}) {
  const [input, setInput] = React.useState('');
  const [invalid, setInvalid] = React.useState('');

  return (
    <div className='teal lighten-5' style={{textAlign: 'center', margin: '1em', padding: '0.5em', paddingTop: '1em'}}>
      <h5>Todo List</h5>

      {tasks.length === 0? <h6 style={{padding: '0.65em'}}>No tasks yet, add some below!</h6>
      : <Row style={{marginBottom: '0'}}>
        {tasks.map((task, i) => 
          <>
            <Col s={9} m={10} l={10} xl={11} style={{textAlign: 'left', marginTop: '0.75em', marginBottom: '0.5em'}}>{task.done? <s>{i+1}.&nbsp;{task.value}</s> : <>{i+1}.&nbsp;{task.value}</> }</Col>
            <Col s={3} m={2} l={2} xl={1} style={{marginTop: '0.75em', marginBottom: '0.5em', textAlign: 'right'}}><Checkbox label={task.done? 'Done': 'Todo'} checked={task.done} value='done' onClick={e => onTasksChange( tasks.map((task, j) => { if(i ===j)return {...task, done: e.target.checked}; return task;}))}></Checkbox></Col>
            <hr style={{width: 'calc(100% - 1.4em)'}}/>
          </>
        )}
      </Row>
      }
      <Row style={{display: 'flex', alignItems: 'center', height: '5em'}}>
        <TextInput s={9} m={10} l={10} xl={11}
          className={invalid}
          label="New task"
          error="Tasks cannot be blank"
          value={input}
          onChange={e => {setInput(e.target.value); setInvalid('')}}
          style={{fontSize: '200'}}
          // validate
          // required
          // ariaRequired='true'
          onKeyDown={(e) => { if (input && e.key==='Enter') { onTasksChange([...tasks, {value: input, done: false}]); setInput(''); } else if(e.key==="Enter") setInvalid('invalid')}}
        />
        <Col s={3} m={2} l={2} xl={1}> 
        {/* <Button
          node="button"
          style={{
            marginRight: '5px'
          }}
          waves="light"
        >
          button
          <Icon left>
            add
          </Icon>
        </Button> */}
        <Button
          floating
          icon={<Icon>add</Icon>}
          small
          node="button"
          waves="light"
          tooltip="Add task"
          onClick={() => {
            if (input) { onTasksChange([...tasks, {value: input, done: false}]); setInput(''); } else setInvalid('invalid');
          }}
        />
        </Col>
      </Row>
      {/* <TextInput
        id="TextInput-49"
        label="Task 2"
      />
      <Checkbox></Checkbox> */}
    </div>
  );
}